main {
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0;
  width: 100%;
  background-color: white;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
  position: relative;
}

body {
  background-color: whitesmoke;
}

.leftbar {
  display: none;
  position: relative;
  max-width: 200px;
  width: 100%;
  text-align: left;
  padding: 10px;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.5);
  background-color: white;
  transition: all 0.3s;
}

.leftbar.open-menu {
  display: block;
}

.card-grid-competition {
  flex: 1;
  display: grid;
  grid-template-columns: 200px 200px 200px;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 20px;
}

.filter {
  display: flex;
  align-items: center;
  border: 2px solid gray;
  border-radius: 40px;

  margin: 0 40px;
  padding: 8px 10px;
  cursor: pointer;
  transition: all 0.3s;
}

.filter:hover {
  background-color: whitesmoke;
}

.filter img {
  width: 40px;
  margin: 0px 5px;
}

.search {
  padding: 10px;
  border: 2px solid gray;
  border-radius: 10px;
}

.dropdown {
  border: 2px solid gray;
  border-radius: 50px;
  margin: 0px 40px;
  position: relative;
  display: flex;
  flex-direction: row-reverse;

}

.sort-details {
  padding: 10px;
  white-space: nowrap
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 40px;
  background-color: rgb(241, 241, 241);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 100;
}

.dropdown-content p {
  padding: 12px 16px;
  display: block;
  text-decoration: none;
  color: black;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

@media (max-width: 576px) {
  .card-grid-competition {
    grid-template-columns: 280px;
  }

  .filter,
  .dropdown {
    margin: 0 10px;
  }

  .filter img {
    width: 30px;
  }

}

@media (min-width: 577px) and (max-width: 768px) {
  .card-grid-competition {
    grid-template-columns: 260px 260px;
  }

  .leftbar {
    display: none;
  }

  .filter img {
    width: 30px;
  }

}

@media (min-width: 769px) and (max-width: 1024px) {
  .card-grid-competition {
    grid-template-columns: 260px 260px 260px;
  }

}

@media (min-width: 1025px) {
  .card-grid-competition {
    grid-template-columns: 280px 280px 280px;
    gap: 60px;
  }

}

/* left side navigation */

.sidenav {
  height: 100%;
  width: 0;
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  background-color: rgb(233, 233, 233);

  overflow: hidden;
  transition: all 0.3s;
}

.sidenav.open {
  width: 250px;
}

form {
  margin-top: 60px;
}

.filters-theme {
  color: #404040;
  font-size: 18px;
  margin-left: 5px;
}

.filters-container {
  padding-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

form h2 {
  color: #404040;
  margin: 10px 0;
  text-align: left;
  padding-left: 10px;
}

.sidenav button,
.sidenav a,
.sidenav label {
  cursor: pointer;
  /* padding: 10px 20px; */
  text-align: left;
  color: #404040;
  /* display: block; */
  transition: 0.3s;
}

.sidenav button:hover,
.sidenav a:hover,
.sidenav label:hover {
  color: hsl(0, 0%, 95%);
}

.sidenav .closebtn {
  background-color: none;
  position: absolute;
  border: none;
  background-color: transparent;
  top: 25px;
  right: 25px;
  scale: 2.5;
  transition: all 0.3s;
}

.sidenav .closebtn:hover{
  color: black;
 }

.sidenav .searchbtn {
  margin-top: 20px;
  padding: 10px 16px;
  border: none;
  border-radius: 5px;
  color: #404040;
  font-size: 14px;
  background-color: hsl(0, 0%, 100%);
  cursor: pointer;
  transition: 0.15s;
}

.sidenav .searchbtn:hover,
.sidenav .searchbtn:active {
  color: hsl(0, 0%, 0%);
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
}

@media screen and (max-height: 576px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav a {
    font-size: 18px;
  }
}

