* {
  margin: 0;
  padding: 0;
  color: black;
  list-style: none;
  text-decoration: none;
}

footer {
  overflow: hidden;
  padding: 40px 0px;
  box-sizing: border-box;
  margin-top: 40px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  background-color: white;
}

.inner-footer {
  display: block;
  margin: 0 auto;
  width: 1100px;
  height: 100%;
}

.logo-container {
  width: 35%;
  float: left;
  display: block;
}

.logo-container img {
  width: 70px;
}

.footer-third {
  width: calc(30% - 20px);
  margin-right: 10px;
  float: left;
}

.footer-third:last-child {
  margin-right: 0;
}

.footer-third h1 {
  font-size: 22px;
  margin-bottom: 20px;
}

.footer-third a {
  font-size: 18px;
  display: block;
  font-weight: 200;
  padding-bottom: 5px;
}

@media(max-width: 600px) {
  .inner-footer {
    width: 90%;
  }

  .inner-footer .footer-third,
  .inner-footer .logo-container {
    width: 100%;
    margin-bottom: 30px;
  }
}

@media (min-width: 600px) and (max-width: 1024px) {
  .inner-footer {
    width: 70%;
  }
}